export default {
	name: 'TheHeader',
	data: function() {
		return {
			isOpen: false
		};
	},
	methods: {
		toggleNavbar: function() {
			this.isOpen = !this.isOpen;
		},
		closeNavbar: function() {
			this.isOpen = false;
		}
	},
	computed: {
		availableLocales() {
			return this.$i18n.locales.filter((i) => i.code !== this.$i18n.locale);
		}
	},
	watch: {
		$route() {
			this.closeNavbar();
		}
	},
	async fetch() {
		this.isOpen = false;
	}
};
